import { Component } from '@angular/core';
import { IonicPage, NavController, MenuController, ViewController, NavParams, LoadingController, Platform, AlertController } from 'ionic-angular';
import { Properties, Cities, Api } from '../../providers';

@IonicPage()
@Component({
  selector: 'page-properties',
  templateUrl: 'properties.html',
})
export class PropertiesPage {
  cardItems: any = [];
  btnDetail: boolean = false;

  public items: any = [];
  public type: any;

  constructor(public viewCtrl: ViewController, public loadingCtrl: LoadingController, public api: Api, public cities: Cities, public NavParams: NavParams, public properties: Properties, public menu: MenuController, private navCtrl: NavController) {
    this.type = this.NavParams.data.length > 0 ? this.NavParams.data : false;
    console.log('constructor', this.type)
  }

  ionViewDidEnter() {
    console.log('ionViewDidEnter')
    console.log('ionViewDidEnter', this.type)

    this.loadProperties(this.type);
    this.initializeItems();
    this.menu.enable(true);
  }

  initializeItems() {
    console.log('initializeItems')

    this.items = this.cardItems;
  }

  /**Filtra la lista en base al searchbar */
  getItems(ev: any) {
    console.log('getItems')

    this.items = this.cardItems;
    const val = ev.target.value;

    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {

        if (item.namePropertyEs.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;
        } else if (item.folio.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        } else if (item.operation.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        } else if (item.city.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        }



      })
    }
  }

  /**Detalles de una propiedad */
  detail(id) {
    console.log('detail', id)

    let loading = this.loadingCtrl.create({
      content: `
        <div class="custom-spinner-container" style="background-color: black;">
          <div class="custom-spinner-box" style="background-color: black;"></div>
        </div>`
    });
    loading.present();


    this.properties.query(id).subscribe(data => {
      this.cities.flag = 2;
      this.properties.property = data.json().data;
      this.navCtrl.push('PropertyDetailPage', 2);
      this.btnDetail = false;
      loading.dismiss();
    });


  }

  loadProperties(type) {
    console.log('loadProperties')

    var $this = this;
    this.cardItems = [];

    this.properties.properties.forEach((value, key, index) => {



      var integer = 0;
      var integer2 = 0;

      if (value.descriptionEs.length > 130) {
        value.descriptionEs = value.descriptionEs.substring(0, 145);
        value.descriptionEs = value.descriptionEs + '...';
      }

      if (this.api.filtreProperties.type) {
        if (!(this.api.filtreProperties.type == value.typepropertyid)) {
          return;
        }
      }

      //Tipo de moneda
      integer = parseInt(this.api.filtreProperties.moneda, 10);
      integer2 = parseInt(value.currencyid, 10);

      if (this.api.filtreProperties.moneda) {
        if (!(integer2 == integer)) {
          return;
        }
      }
      //-

      //Precio minimo
      integer = parseInt(this.api.filtreProperties.priceMin, 10);
      integer2 = parseInt(value.price, 10);

      if (this.api.filtreProperties.priceMin && value.price) {
        if (!(integer <= integer2)) {
          return;
        }
      }
      //-

      //Precio maximo
      integer = parseInt(this.api.filtreProperties.priceMax, 10);
      integer2 = parseInt(value.price, 10);

      if (this.api.filtreProperties.priceMax && value.price) {
        if (!(integer >= integer2)) {
          return;
        }
      }
      //-

      //Recamaras minimas
      integer = parseInt(this.api.filtreProperties.bedMin, 10);
      integer2 = parseInt(value.bedrooms, 10);
      if (this.api.filtreProperties.bedMin && value.bedrooms) {
        if (!(integer <= integer2)) {
          return;
        }
      }
      //-

      //Recamaras maximas
      integer = parseInt(this.api.filtreProperties.bedMax, 10);
      integer2 = parseInt(value.bedrooms, 10);

      if (this.api.filtreProperties.bedMax && value.bedrooms) {
        if (!(integer >= integer2)) {
          return;
        }
      }
      //-

      //tipo de operacion
      if (this.api.filtreProperties.operation) {
        if (!(this.api.filtreProperties.operation == value.operation)) {
          return;
        }
      }


      //Metros 2 minimos
      integer = parseInt(this.api.filtreProperties.m2Min, 10);
      integer2 = parseInt(value.m2c, 10);

      if (this.api.filtreProperties.m2Min && value.m2c) {

        if (!(integer <= integer2)) {
          return;
        }
      }
      //-

      //Metros 2 Maximos
      integer = parseInt(this.api.filtreProperties.m2Max, 10);
      integer2 = parseInt(value.m2c, 10);

      if (this.api.filtreProperties.m2Max && value.m2c) {

        if (!(integer >= integer2)) {
          return;
        }
      }






      if (type) {
        if (value.stateid == type) {

          $this.cardItems.push(value);
        } else {
          return;
        }
      } else {
        $this.cardItems.push(value);

      }



    });

  }

  pop() {
    //this.navCtrl.setRoot('CitiesPage');
    this.navCtrl.pop();

  }

  /**Abrir pagina de filtros */
  filtre() {
    console.log('filtre')
    console.log(this.type)
    this.navCtrl.push('FiltrePage', { page: 'properties', state: this.type });
  }


}











